<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
transition-group(
  :appear="true"
  :class=`[
    "c-popover-layout",
    "c-popover-layout--" + position,
    "c-popover-layout--" + theme,
    "u-no-drag",
    "u-reset-widget",
    "js-editor-transform",
    {
      "c-popover-layout--editor": editor
    }
  ]`
  :enter-active-class="'u-animated' + ' ' + (transition ? 'u-ms500' : 'u-ms0') + ' ' + 'u-fade-in-' + position.split('-')[1]"
  :leave-active-class="'u-animated' + ' ' + (transition ? 'u-ms500' : 'u-ms0') + ' ' + 'u-fade-out-' + position.split('-')[1]"
  tag="div"
)
  div(
    v-if="triggered && closed === false"
    class="c-popover-layout__widget"
    key="widget"
  )
    widget-close(
      @close="onClose"
      class="c-popover-layout__close"
      theme="light"
    )

    .c-popover-layout__box
      slot(
        name="header"
      )

      slot(
        name="body"
      )

      widget-branding(
        v-if="!subscribed"
        :theme="theme"
        class="c-popover-layout__branding"
      )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: COMPONENTS
import WidgetBranding from "@/components/widgets/WidgetBranding"
import WidgetClose from "@/components/widgets/WidgetClose"

// PROJECT: MIXINS
import CloseChildMixin from "@/mixins/CloseChildMixin"
import EditorMixin from "@/mixins/EditorMixin"
import PositionMixin from "@/mixins/PositionMixin"
import SubscriptionMixin from "@/mixins/SubscriptionMixin"
import ThemeMixin from "@/mixins/ThemeMixin"
import TransitionMixin from "@/mixins/TransitionMixin"
import TriggerChildMixin from "@/mixins/TriggerChildMixin"

export default {
  components: {
    WidgetBranding,
    WidgetClose
  },

  mixins: [
    CloseChildMixin,
    EditorMixin,
    PositionMixin,
    SubscriptionMixin,
    ThemeMixin,
    TransitionMixin,
    TriggerChildMixin
  ]
}
</script>

